<template>
  <div class="game-container">
    <!-- Игровая информация -->
    <div class="game-info">
      <div>Score: {{ score }}₿</div>
      <div>Lives: {{ lives }}</div>
      <div>Time: {{ timeLeft }}s</div>
    </div>

    <!-- Игровое поле -->
    <div class="game-field" ref="gameField" @click="handleClick">
      <!-- Кот -->
      <img 
        :src="catImage" 
        class="cat"
        :style="{
          transform: `translate(-50%, -50%) scale(${catScale})`
        }"
        alt="Cat"
      />
      
      <!-- Монеты -->
      <template v-for="coin in coins" :key="coin.id">
        <img 
          :src="coinImage"
          class="coin"
          :style="{
            left: coin.x + 'px',
            top: coin.y + 'px',
            width: coin.size + 'px',
            height: coin.size + 'px'
          }"
          alt="Coin"
        />
      </template>
      
      <!-- Бонусный часы -->
      <img v-if="showClock" 
           @click="addBonusTime" 
           class="bonus-clock" 
           :style="clockStyle"
           :src="clockImage" 
           alt="bonus clock">
    </div>

    <!-- Статус игры и кнопки -->
    <div class="game-status">
      <template v-if="gameOver">
        <template v-if="isFirstGame">
          <h2>Let's play and PUMP the cat!</h2>
        </template>
        <template v-else>
          <h2 class="game-over-title">BITCAT PUMPED!</h2>
          <p>Your score: {{ score }}₿</p>
        </template>
        <button class="play-button" @click="startGame">{{ isFirstGame ? 'Play' : 'Play Again' }}</button>
        <button v-if="!isPlaying && !isFirstGame" class="share-button" @click="showShare = true">Share</button>
        <button v-if="isFirstGame" class="rules-button" @click="showRules = true">Rules</button>
      </template>
    </div>
    <RulesPopup v-model="showRules" />
    <SharePopup v-model="showShare" :score="score" />
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted, computed } from 'vue'
import catImage from '@/assets/bitcat.webp'
import coinImage from '@/assets/coin.png'
import clockImage from '@/assets/clock.png'
import RulesPopup from '@/components/RulesPopup.vue'
import SharePopup from '@/components/SharePopup.vue'
import store from '@/vuex'

// Игровые константы
const INITIAL_LIVES = 9 // Начальное количество жизней
const GAME_DURATION = 30 // Длительность игры в секундах
const MIN_COIN_SIZE = 30 // Минимальный размер монеты
const MAX_COIN_SIZE = 80 // Максимальный размер монеты
const MIN_COIN_LIFETIME = 1000 // Минимальное время жизни монеты (мс)
const MAX_COIN_LIFETIME = 3000 // Максимальное время жизни монеты (мс)
const COIN_SPAWN_INTERVAL = 1000 // Интервал появления монет (мс)
const MIN_POINTS = 10 // Минимальное количество очков за монету
const MAX_POINTS = 50 // Максимальное количество очков за монету
const MISS_PENALTY = 0.1 // Штраф за пропуск монеты (10%)
const MIN_CAT_SCALE = 0.5 // Минимальный размер кота (50% от исходного)
const MAX_CAT_SCALE = 2.5 // Максимальный размер кота (250% от исходного)
const SCORE_FOR_MAX_SCALE = 500 // Счет для максимального размера кота
const MISS_SCALE_PENALTY = 20 // Уменьшение счета при промахе (очков)
const INITIAL_COINS_COUNT = 5 // Количество монет при старте игры
const BONUS_CLOCK_SPAWN_INTERVAL = 10000 // Интервал появления бонусных часов (мс)
const BONUS_TIME = 10 // Дополнительное время за бонусные часы (с)
const GAME_WIDTH = 800 // Ширина игрового поля
const GAME_HEIGHT = 600 // Высота игрового поля

// Состояние игры
const score = ref(0)
const lives = ref(INITIAL_LIVES)
const timeLeft = ref(GAME_DURATION)
const gameOver = ref(true)
const isFirstGame = ref(true)
const coins = ref([])
const gameField = ref(null)
const showClock = ref(false)
const clockTimer = ref(null)
const bonusClockTimer = ref(null)
const clockStyle = ref({
  left: '50%',
  top: '50%'
})
const showRules = ref(false)
const showShare = ref(false)
const isPlaying = ref(false)
const gameInterval = ref(null)

// Вычисляемый размер кота на основе счета
const catScale = computed(() => {
  // Линейная интерполяция между MIN_CAT_SCALE и MAX_CAT_SCALE
  const scale = MIN_CAT_SCALE + (score.value / SCORE_FOR_MAX_SCALE) * (MAX_CAT_SCALE - MIN_CAT_SCALE)
  // Ограничиваем значение между минимальным и максимальным
  return Math.min(MAX_CAT_SCALE, Math.max(MIN_CAT_SCALE, scale))
})

// Игровые таймеры
let gameTimer = null

// Уменьшение размера кота при промахе
const reduceCatSize = () => {
  // Уменьшаем счет на фиксированное количество очков
  score.value = Math.max(0, score.value - MISS_SCALE_PENALTY)
}

// Получение случайной позиции для монеты
const getRandomPosition = () => {
  if (!gameField.value) return { x: 0, y: 0 }
  const field = gameField.value.getBoundingClientRect()
  return {
    x: Math.random() * (field.width - MAX_COIN_SIZE),
    y: Math.random() * (field.height - MAX_COIN_SIZE)
  }
}

// Создание новой монеты
const createCoin = () => {
  if (!gameField.value) return null // Не создаем монету, если поле не готово
  
  const size = MIN_COIN_SIZE + Math.random() * (MAX_COIN_SIZE - MIN_COIN_SIZE)
  const points = Math.round(MAX_POINTS - (size - MIN_COIN_SIZE) / (MAX_COIN_SIZE - MIN_COIN_SIZE) * (MAX_POINTS - MIN_POINTS))
  const position = getRandomPosition()
  
  const coin = {
    id: Date.now() + Math.random(), // добавляем случайность для уникальности
    x: position.x,
    y: position.y,
    size,
    points
  }
  
  // Удаление монеты через случайное время
  const lifetime = MIN_COIN_LIFETIME + Math.random() * (MAX_COIN_LIFETIME - MIN_COIN_LIFETIME)
  setTimeout(() => {
    if (!gameOver.value) {
      // Если не успели поймать монету, отнимаем 10% от ее номинала
      const penalty = Math.round(coin.points * MISS_PENALTY)
      score.value = Math.max(0, score.value - penalty)
      removeCoin(coin.id)
      checkGameOver()
    }
  }, lifetime)

  return coin
}

// Удаление монеты
const removeCoin = (id) => {
  coins.value = coins.value.filter(coin => coin.id !== id)
}

// Обработка клика
const handleClick = (event) => {
  if (gameOver.value) return
  
  const rect = gameField.value.getBoundingClientRect()
  const x = event.clientX - rect.left
  const y = event.clientY - rect.top
  
  // Проверяем, что клик был внутри игрового поля
  if (x < 0 || x > rect.width || y < 0 || y > rect.height) {
    return
  }
  
  let hit = false
  
  coins.value.forEach(coin => {
    const coinCenterX = coin.x + coin.size / 2
    const coinCenterY = coin.y + coin.size / 2
    const distance = Math.sqrt(
      Math.pow(x - coinCenterX, 2) + 
      Math.pow(y - coinCenterY, 2)
    )
    
    if (distance <= coin.size / 2) {
      hit = true
      score.value += coin.points
      removeCoin(coin.id)
    }
  })
  
  if (!hit) {
    // При промахе только уменьшаем количество жизней
    lives.value--
    checkGameOver()
  }
}

// Проверка окончания игры
const checkGameOver = () => {
  if (lives.value <= 0) {
    endGame()
  }
}

// Создание начальных монет
const createInitialCoins = () => {
  if (!gameField.value) return [] // Не создаем монеты, если поле не готово
  
  const initialCoins = []
  for (let i = 0; i < INITIAL_COINS_COUNT; i++) {
    const coin = createCoin()
    if (coin) initialCoins.push(coin)
  }
  return initialCoins
}

// Инициализация игры
const initializeGame = () => {
  score.value = 0
  lives.value = INITIAL_LIVES
  timeLeft.value = GAME_DURATION
  coins.value = createInitialCoins() // Создаем новые монеты при старте игры
}

// Начало игры
const startGame = () => {
  if (gameInterval.value) {
    clearInterval(gameInterval.value)
  }
  if (clockTimer.value) {
    clearInterval(clockTimer.value)
  }
  if (bonusClockTimer.value) {
    clearInterval(bonusClockTimer.value)
  }

  document.body.style.overflow = 'hidden'
  initializeGame()
  gameOver.value = false
  isPlaying.value = true
  isFirstGame.value = false

  // Запуск таймеров
  gameInterval.value = setInterval(() => {
    if (timeLeft.value > 0) {
      timeLeft.value--
    } else {
      endGame()
    }
  }, 1000)

  clockTimer.value = setInterval(() => {
    coins.value.push(createCoin())
  }, COIN_SPAWN_INTERVAL)

  bonusClockTimer.value = setInterval(() => {
    clockStyle.value = getRandomClockPosition()
    showClock.value = true
    setTimeout(() => {
      if (showClock.value) {
        showClock.value = false
      }
    }, 1000)
  }, BONUS_CLOCK_SPAWN_INTERVAL)
}

// Окончание игры
const endGame = () => {
  gameOver.value = true
  clearInterval(gameInterval.value)
  clearInterval(clockTimer.value)
  clearInterval(bonusClockTimer.value)
  store.commit('updateScores', score.value)
  coins.value = createInitialCoins() // Создаем новые монеты после окончания игры
  document.body.style.overflow = 'auto'
  isPlaying.value = false
}

// Добавление бонусного времени
const addBonusTime = () => {
  timeLeft.value += BONUS_TIME
  showClock.value = false
}

// Генерация случайной позиции для часов
const getRandomClockPosition = () => {
  const gameField = document.querySelector('.game-container')
  if (!gameField) return { left: '50%', top: '50%' }
  
  const fieldRect = gameField.getBoundingClientRect()
  const padding = 20 // отступ от краев
  
  const left = Math.random() * (fieldRect.width - padding * 2) + padding
  const top = Math.random() * (fieldRect.height - padding * 2) + padding
  
  return {
    left: left + 'px',
    top: top + 'px',
    transform: 'none' // Убираем transform, так как теперь используем абсолютные координаты
  }
}

// Монтирование компонента
onMounted(() => {
  // Ждем немного, чтобы DOM полностью загрузился
  setTimeout(() => {
    coins.value = createInitialCoins()
  }, 100)

  window.addEventListener('resize', () => {
    if (!gameField.value) return // Проверяем наличие поля
    coins.value = []
    if (!gameOver.value) {
      coins.value = createInitialCoins()
    } else {
      coins.value = createInitialCoins()
    }
  })
})

// Размонтирование компонента
onUnmounted(() => {
  clearInterval(gameInterval.value)
  clearInterval(clockTimer.value)
  clearInterval(bonusClockTimer.value)
  // Разблокируем прокрутку при уходе с компонента
  document.body.style.overflow = 'auto'
})
</script>

