import { createStore } from 'vuex';

export const store = createStore({
    state () {
        return {
            autoConnect: true,
            userSOLBalance: 0,
            boardType: 'score',
            userTag: localStorage.getItem('userTag') || '',
            bestScore: parseInt(localStorage.getItem('bestScore')) || 0,
            totalScore: parseInt(localStorage.getItem('totalScore')) || 0,
            score: parseInt(localStorage.getItem('score')) || 0,
            weight: parseInt(localStorage.getItem('weight')) || 0,
            multiplayerStats: {
                wins: parseInt(localStorage.getItem('wins')) || 0,
                loses: parseInt(localStorage.getItem('loses')) || 0
            },
            stats: {
                strength: parseInt(localStorage.getItem('strength')) || 1,
                agility: parseInt(localStorage.getItem('agility')) || 1,
                stamina: parseInt(localStorage.getItem('stamina')) || 1,
                luck: parseInt(localStorage.getItem('luck')) || 1,
                intelligence: parseInt(localStorage.getItem('intelligence')) || 1
            }
        }
    },
    mutations: {
        setBalance(state, payload) {
            state.userSOLBalance = payload.balance
        },
        setBoardType(state, payload) {
            console.log(payload)
            state.boardType = payload.boardType
        },
        setStat(state, { stat, value }) {
            state.stats[stat] = value
            localStorage.setItem(stat, value)
        },
        updateScores(state, score) {
            // Update total score
            state.totalScore += score;
            localStorage.setItem('totalScore', state.totalScore.toString());
            
            // Update best score if new score is higher
            if (score > state.bestScore) {
                state.bestScore = score;
                localStorage.setItem('bestScore', score.toString());
            }
        }
    },
    getters: {
        getBoardType: state => state.boardType,
        userTag: state => state.userTag,
        stats: state => state.stats,
        bestScore: state => state.bestScore,
        totalScore: state => state.totalScore
    }
});
