</template>



<script setup>
import { WalletMultiButton } from 'solana-wallets-vue'
import { ref, computed } from 'vue'
import store from '@/vuex'
import bitcatImage from '@/assets/bitcat.webp'

// const store = useStore()

console.log('store.getters.userTag')
console.log(store.getters.userTag)

const userTag = ref(store.getters.userTag || '')
const isEditing = computed(() => store.getters.userTag !== '')
const stats = computed(() => store.getters.stats)
// const canUpgrade = computed(() => userTag.value !== '')
const canUpgrade = computed(() => false)
const bestScore = computed(() => store.getters.bestScore)
const totalScore = computed(() => store.getters.totalScore)
const weight = computed(() => store.state.weight)
const multiplayerStats = computed(() => store.state.multiplayerStats)

const saveUserTag = () => {
  if (userTag.value.trim()) {
    store.commit('setUserTag', userTag.value.trim())
  }
}

const upgradeStat = (stat) => {
  if (stats.value[stat] < 10) {
    store.commit('setStat', { stat, value: stats.value[stat] + 1 })
  }
}
</script>

